import {usersAPI} from "../api/api";

const TOGGLE_FOLLOW = 'samurai-network/users/TOGGLE_FOLLOW'
const SET_USERS = 'samurai-network/users/SET_USERS'
const SET_CURRENT_PAGE = 'samurai-network/users/SET_CURRENT_PAGE'
const SET_TOTAL_USERS_COUNT = 'samurai-network/users/SET_TOTAL_USERS_COUNT'
const TOGGLE_IS_FETCHING = 'samurai-network/users/TOGGLE_IS_FETCHING'
const TOGGLE_IS_FOLLOWING_PROGRESS = 'samurai-network/users/TOGGLE_IS_FOLLOWING_PROGRESS'

let initialstate = {
    users: [],
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: []
}

export const usersReducer = (state = initialstate, action) => {

    switch (action.type) {
        case TOGGLE_FOLLOW:
            return{...state,
            users:
            //updateObjectInArray(state.users, action.userID, "id", {followed: !u.followed})
                state.users.map(u => {
                if(u.id === action.userID) {
                    return {...u, followed: !u.followed}
                }
                return u;
            })
            }
        case SET_USERS:
            return {...state, users: action.users}
        case SET_CURRENT_PAGE:
            return {...state, currentPage: action.currentPage}
        case SET_TOTAL_USERS_COUNT:
            return {...state, totalUsersCount: action.count}
        case TOGGLE_IS_FETCHING:
            return {...state, isFetching: action.isFetching}
        case TOGGLE_IS_FOLLOWING_PROGRESS:
            return {...state,
                followingInProgress: action.isFetching ?
                    [...state.followingInProgress, action.userID]
                    : state.followingInProgress.filter(id => id != action.userID)}
        default:
            return state;
    }
}
export const toggleFollowSuccess = (userID) => ({type: TOGGLE_FOLLOW, userID})
export const setUsers = (users) => ({type: SET_USERS, users})
export const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage})
export const setTotalUsersCount = (totalCount) => ({type: SET_TOTAL_USERS_COUNT, count: totalCount})
export const toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching})
export const toggleFollowingProgress = (isFetching, userID) => ({type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, userID})

export const requestUsers = (page, pageSize) => {
   return async (dispatch) => {
        dispatch(toggleIsFetching(true));
        dispatch(setCurrentPage(page));
        const data = await usersAPI.getUsers(page, pageSize);
            dispatch(toggleIsFetching(false));
            dispatch(setUsers(data.items));
            dispatch(setTotalUsersCount(data.totalCount));
    }
}

export const toggleFollow = (userId, alreadyFollowed) => {
    return (dispatch) => {

        const actionToggle = (response => {                 //Not to double my code
            if (response.data.resultCode === 0) {
                dispatch(toggleFollowSuccess(userId));
            }
            dispatch(toggleFollowingProgress(false, userId));
        });

        dispatch(toggleFollowingProgress(true, userId));
        if (!alreadyFollowed) {
            usersAPI.follow(userId)
                .then(actionToggle);
        } else {
            usersAPI.unfollow(userId)
                .then(actionToggle);
        }

    }
}


export default usersReducer;