import React from "react";
import styles from "./users.module.css";
import userPhoto from "../../assets/images/306473.png";
import {NavLink} from "react-router-dom";

let User = ({user, followingInProgress, toggleFollow}) => {
    return (
        <div className={styles.users_wrapper}>
                <span className={styles.follow__block}>
                    <div>
                        <NavLink to={'../profile/' + user.id}>
                        <img src={user.photos.small !== null ? user.photos.small : userPhoto} alt={user.name}
                             className={styles.userPhoto}/>
                        </NavLink>
                    </div>
                    <div>
                        <button disabled={followingInProgress.some(id => id === user.id)}
                                onClick={() => {
                                    toggleFollow(user.id, user.followed)
                                }}>
                            {user.followed ? "Unfollow" : "Follow"}</button>
                    </div>
                </span>
            <span className={styles.user_info__block}>
                    <span className={styles.status_block}>
                        <div>{user.name}</div><div>{user.status}</div>
                    </span>
                    <span>
                        <div>{'user.location.country'}</div><div>{'user.location.city'}</div>
                    </span>
                </span>
        </div>
    )
}

export default User;