import React from "react";
import {Form, Formik} from "formik";
import * as Yup from 'yup'
import {MyCheckbox, MyTextInput} from "../Forms/FormsControl";
import {connect} from "react-redux";
import {errorMessage, login} from "../../redux/auth-reducer";
import {Navigate} from "react-router-dom";
import {setStatus} from "../../redux/profile-reducer";
import styles from "./../Forms/FormsControl.module.css"

const LoginForm = ({validationSchema, onSubmit}) => {
    return (
        <Formik initialValues={{
            email: '',
            password: '',
            rememberMe: false,
        }} validationSchema={validationSchema} onSubmit={onSubmit}
        >
            {({status}) =>
                <Form>
                    <div>
                        <MyTextInput name={"email"} placeholder={"Login"} />
                    </div>

                    <div>
                        <MyTextInput name={"password"} type={"password"} placeholder={"Password"} />
                    </div>

                    <div>
                        <MyCheckbox name={"rememberMe"}>remember me</MyCheckbox>
                    </div>
                    {status && <div className={styles.server_error_message}>{status}</div>}
                    <div>
                        <button type={"submit"}>Login</button>
                        <button type={"reset"}>Reset</button>
                    </div>
                </Form>
            }
        </Formik>
    );
}

const Login = (props) => {

    const onSubmit = (values, actions) => {
        props.login(values.email, values.password, values.rememberMe,  actions.setStatus);
        actions.setSubmitting(false);
    };

    const validationSchema = Yup.object({
        email: Yup.string()
            .email('Invalid email')
            .required('Required'),
        password: Yup.string()
            .max(20, 'Must be 20 characters or less')
            .required('Required'),
    });

    if (props.isAuth) {
        return <Navigate to={"/profile"} />
    };

    return (
        <div>
            <h1>Login</h1>
            <LoginForm onSubmit={onSubmit} validationSchema={validationSchema} />
        </div>
    );
}

const mapStateToProps = (state) => ({
    isAuth:state.auth.isAuth
})
export default connect(mapStateToProps, {login})(Login);

