import React, {useEffect, useState} from "react";
import styles from "../../Common/Paginator/Paginator.module.css";

let Paginator = ({totalItemsCount, currentPage, onPageChanged, pageSize, portionSize = 10}) => {
    let pagesCount = Math.ceil(totalItemsCount / pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }
    let portionCount = Math.ceil(pagesCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionPageNumber = (portionNumber - 1) * portionSize +1;
    let rightPortionPageNumber = portionNumber * portionSize;

    useEffect(() =>
        setPortionNumber(Math.ceil(currentPage / portionSize)), [currentPage]
    )

    return <div className={styles.paginator}>
        { portionNumber > 1 &&
            <button onClick={() => {setPortionNumber(portionNumber - 1)}}>PREV</button>}

        {pages.filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
            .map(p => {
                return <span className={currentPage === p ? styles.selected_page : styles.unselected_page}
                key={p} onClick={(e) => {
                onPageChanged(p)}
                }>{p}</span>
            })}

        {portionCount > portionNumber &&
            <button onClick={() => {setPortionNumber(portionNumber + 1)}}>NEXT</button>}
    </div>
}

export default Paginator;