export let initialstate = {
    chatmatePeopleData: [
        {
            id: 1,
            name: "Max",
            img: "data:image/jpeg;base64,"
        },
        {
            id: 2,
            name: "Bob",
            img: "data:image/png;base64,"
        },
        {
            id: 3,
            name: "Kate",
            img: "data:image/jpeg;base64,"
        },
        {
            id: 4,
            name: "David",
            img: "data:image/jpeg;base64,"
        },
        {
            id: 5,
            name: "Sarah",
            img: "data:image/jpeg;base64,"
        },
        {
            id: 6,
            name: "Leo",
            img: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQAJRWlu3B1sAjRY5oF7YM6ZOtP6TnmWOU0QQ&usqp=CAU"
        },
        {
            id: 7,
            name: "Ivan",
            img: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQjVcNWJANdBAZBgT6X1MYKl392GEaOsFxVAg&usqp=CAU"
        }
    ],

    conversationData: [
        {id: 1, message: "It's me"},
        {id: 2, message: "Are you there?"},
        {id: 3, message: "Hey"},
        {id: 4, message: "Hey"},
        {id: 5, message: "Hey"}
    ]
};

const dialogReducer = (state = initialstate, action) => {

    switch (action.type) {
        case ADD_MESSAGE:
            let newMessage = {
                id: state.conversationData.length + 1,
                message: action.addMessageForm
            };
            return {
                ...state,
                conversationData: [...state.conversationData, newMessage]
            }
        default:
            return state;
    }
}
export const addMessageActionCreator = (addMessageForm) => ({type: ADD_MESSAGE, addMessageForm: addMessageForm})

export default dialogReducer;