import {profileAPI, usersAPI} from "../api/api";

const ADD_POST = 'samurai-network/profile/ADD-POST';
const SET_USER_PROFILE = 'samurai-network/profile/SET_USER_PROFILE';
const SET_STATUS = 'samurai-network/profile/SET_STATUS';
const DELETE_POST = 'samurai-network/profile/DELETE_POST';

let initialstate = {
    postsData: [
        {id: 1, message: "Hi, how are you?", likes: 15},
        {id: 2, message: "It's my first post here", likes: 12},
    ],
    profileUserData:
        {
            id: 1,
            name: "John Smith",
            img: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT36SIUCmgmmgLkfZEeT0QCwcOBCK5UtO1QMQ&usqp=CAU",
            birthday: "1 of April",
            city: "Tel-Aviv",
            education: "The Univerity '11",
        },
    profile: null,
    status: ""
}

export const profileReducer = (state = initialstate, action) => {

    switch (action.type) {
        case ADD_POST: {
            let newPost = {
                id: state.postsData.length,
                message: action.newPostForm,
                likes: 0
            };
            return {
                ...state,
                newPostText: "",
                postsData: [...state.postsData, newPost]
            }
        }
        case SET_USER_PROFILE: {
            return {...state, profile: action.profile}
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
        case DELETE_POST: {
            return {
                ...state,
                postsData: state.postsData.filter(p => p.id != action.postId)
            }
        }
        default:
            return state;
    }
}
export const addPostActionCreator = (newPostForm) => ({type: ADD_POST, newPostForm: newPostForm})
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile})
export const setStatus = (status) => ({type: SET_STATUS, status})
export const deletePost = (postId) => ({type: DELETE_POST, postId})

export const getUserProfile = (userId) => async (dispatch) => {
    const response = await usersAPI.getProfile(userId);
    dispatch(setUserProfile(response.data));
}

export const getStatus = (userId) => async (dispatch) => {
    const response = await profileAPI.getStatus(userId)
    dispatch(setStatus(response.data));
}

export const updateStatus = (status) => async (dispatch) => {
    const response = await profileAPI.updateStatus(status);
    if (response.data.resultCode === 0) {
        dispatch(setStatus(status));
    }
}

export default profileReducer;